@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --color-primary: #212121;
    --color-secondary: #ffc200;
    --color-tertiary: #5b5b5b;
    --color-white: #fff;
    --boton-padding: 15px 30px;
}


/* Dark Mode */

/* :root {
    --color-primary: #fff;
    --color-secondary: #ffc200;
    --color-tertiary: #fff;
    --color-white: #212121;
    --boton-padding: 15px 30px;
} */
html {
  scroll-behavior: smooth;
}

body {
    font-family: 'Ubuntu', sans-serif;
    font-weight: bold;
    color: #212121;
    color: var(--color-primary);
    background-color: #fff;
    background-color: var(--color-white);
    padding: 0;
    margin: 0;
}

/* ScrollBar */

body::-webkit-scrollbar {
  width: .5em;
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: #474559;
  outline: 1px solid #eee;
}



/* Buttons */

.button__container {
  display: flex;
}

.button {
  display: inline-block;
  color: #fff;
  color: var(--color-white);
  padding: 15px 30px;
  padding: var(--boton-padding);
  text-decoration: none;
  border-radius: 40px;
  font-size: 0.9rem;
  margin-right: 15px;
  font-weight: bold;
  text-align: center;
  flex: 1 1;
}

.button--resume {
  background: #eee;
  color: #5b5b5b;
  color: var(--color-tertiary)
}

.button--contact {
  background: #474559;
  color: #fff
}

.button--send {
  background: #ffc200;
  background: var(--color-secondary);
  border: none;
  align-self: flex-end;
  margin-right: 0;
}

.button--send:hover {
  background: #474559;
}

.footer__text {
  text-align: center;
  margin-bottom: 20px;
}

/* Animations */

.animation-rotate {
  animation: rotation 5s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

.animation-change-color:hover {
  animation-name: changeColor;
  animation-duration: .5s;
  animation-fill-mode: forwards;
}

@keyframes changeColor {
  100% {
      color:#ffc200;
      color:var(--color-secondary)
  }
}

.animation-change-background:hover {
  animation-name: changeBackground;
  animation-duration: .5s;
  animation-fill-mode: forwards;
}

@keyframes changeBackground {
  100% {
      background: #ffc200;
      background: var(--color-secondary);
  }
}

.body__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100vh - 70px);
}

/* Resposive Web */

@media screen and (max-width: 1000px) {

  :root {
      --boton-padding: 15px 20px
  }

}

@media screen and (max-width: 600px) {

  :root {
      --boton-padding: 15px 30px;
  }

  .body__container {
      margin-top: 30px;
  }

  /* Button */

  .button__container {
      flex-direction: column;
  }

  .button {
      margin: 10px 0;
      width: 100%;
  }

}
.hero__container {
    display: flex;
    align-items: center;
    min-height: calc(100vh - 70px);
}

.hero__texts {
    width: 60%;
    margin-bottom: 50px;
    
}

.hero__title {
    font-size: 2rem;

}

.hero__subtitle {
    font-size: 1rem;
    font-weight: 400;
    margin: 30px 0;
}

.hero__texts > .nav__container--icons {
    display: none;
    margin-bottom: 30px;
}

.hero__image {
    display: flex;
    justify-content: flex-end;
    width: 40%;
}

.hero__image img {
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}
/* Animations */
.shadow-drop-2-center {
	animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
  @keyframes shadow-drop-2-center {
    0% {
      transform: translateZ(0);
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      transform: translateZ(50px);
      box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }


.tracking-in-expand {
    animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}
  @keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
      color: var(--color-secondary);
    }
  }
  

@media screen and (max-width: 1000px) {
    .hero__texts {
        width: 70%;
    }
  
    .hero__image {
        justify-content: center;
        width: 30%;
  
    }
  
    .hero__image img {
        width: 150px
    } 
 
  }
  
  @media screen and (max-width: 600px) {

  
    .hero__container {
        flex-direction: column;
        margin: 30px 0 60px 0;
    }
  
    .hero__texts {
        width: 100%
    }
  
    .hero__title {
        font-size: 1.5rem;
    }
  
    .hero__texts > .nav__container--icons {
        display: inline-block;
    }
  
    .hero__image {
        width: 100%;
    }
  }
/* Stack */

.abilities__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-gap: 20px;
    align-items: center;
    justify-items: center;
    margin: 60px 0;
  }
  
  
  /* .abilities__contianer-icon {
    width: 120px;
    height: 120px;
  } */
  
  .abilities__icon {
    display: inline-block;
    width: 120px;
  }
  
  .tooltip {
    display:inline-block;
    position:relative;
    text-align:left;
  }
  
  .tooltip .top {
    min-width:150px; 
    top:-20px;
    left:50%;
    transform:translate(-50%, -100%);
    padding:10px 20px;
    color: var(--color-white);
    background-color: var(--color-primary);
    font-weight:normal;
    font-size:13px;
    border-radius:8px;
    position:absolute;
    z-index:99999999;
    box-sizing:border-box;
    box-shadow:0 1px 8px rgba(0,0,0,0.5);
    display:none;
  }
  
  .tooltip:hover .top {
    display:block;
  }
  
  .tooltip .top i {
    position:absolute;
    top:100%;
    left:50%;
    margin-left:-12px;
    width:24px;
    height:12px;
    overflow:hidden;
  }
  
  .tooltip .top i::after {
    content:'';
    position:absolute;
    width:12px;
    height:12px;
    left:50%;
    transform:translate(-50%,-50%) rotate(45deg);
    background-color: var(--color-primary);
    box-shadow:0 1px 8px rgba(0,0,0,0.5);
  }

  /* Resposive Web */

@media screen and (max-width: 1000px) {

  /* Abilities */

  .abilities__grid {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
      margin: 40px 0;
  }

  .abilities__icon {
      width: 80px;
  }
}

@media screen and (max-width: 1000px) {

  .abilities__grid {
      grid-template-columns: 1fr 1fr;
